import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';

import { Router, ActivatedRoute } from '@angular/router';
import { AddvendorService } from './addvendor.service'
import { Vendor } from './vendor';

@Component({
  selector: 'app-addvendor',
  templateUrl: './addvendor.component.html',
  styleUrls: ['./addvendor.component.scss']
})
export class AddvendorComponent implements OnInit {

  vendors:Vendor[];
  vendor=new Vendor();
  vendorId: any;

  constructor(private router: Router,private service: AddvendorService, private route:ActivatedRoute) { }

  ngOnInit() {

    this.getVendorById();
    
  }
  addVendor():void{
    this.service.insertVendor(this.vendor)
    .subscribe((response) => {
      console.log(response);
      
       this.getVendor;
      this.router.navigate(['vendor'])
      this.resetForm();
    },(error) => {
      console.log(error);
    });
  }
  resetForm(form?:NgForm){
    if(form!=null)
    form.resetForm();
    this.service.formData={
      
      vendorId: '',
      vendorName:'',
      vendorAddress: '',
      vendorLocation: '',
      service: '',
      pincode: '',
      isActive: '',
      contactId: '',
      contactName:'',
      department: '',
      email: '',
      phone : ''           
    }                                  
  }
  getVendor():void{
    this.service.getAllVendors()
    .subscribe((data)=>{
      this.vendors=data;
      console.log(data);

    },(error)=>{
      console.log(error);

    });
  }

  
  logout(){
    this.service.logout();

  }

  backtoview(){
    this.router.navigate(['/vendor']); 
  }
  
 
  getVendorById(){
    this.vendorId=this.route.snapshot.paramMap.get('id')
    console.log(this.vendorId);
    this.service.getVendorById(this.vendorId)
    .subscribe((vendorbyId)=>{
      this.vendor=vendorbyId;
      console.log(vendorbyId);
    },(error)=>{
      console.log(error);
    });
  }
  

}