import { Component, OnInit } from '@angular/core';

import { VendorService } from '../vendor.service';
import { ActivatedRoute, Router } from '@angular/router';
import { ContactService } from 'src/app/contact.service';
import { Vendor } from 'src/app/addvendor/vendor';
import { AuthService } from 'src/app/auth.service';

@Component({
  selector: 'app-view-contact',
  templateUrl: './view-contact.component.html',
  styleUrls: ['./view-contact.component.scss']
})
export class ContactComponent implements OnInit {
  contact : Vendor[]
  contactId:any;
  id:any;
  SearchText:any;

  constructor(private router:Router,private service:ContactService,private route:ActivatedRoute,private authService:AuthService) { }

  ngOnInit() {
    this.getContacts();
  }
  getContacts():void{
    this.contactId=this.route.snapshot.paramMap.get('id')
    this.service.getAllContacts(this.contactId)
    .subscribe((data)=>{
      this.contact=data;
    });

  }
  backtovendor(){
    this.router.navigate(['/vendor']);
  }
  logout(){
    this.authService.logout();
  
}
backtoview(){
  this.router.navigate(['/vendor']); 
}
}
