import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Vendor } from './vendor';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { Router } from '@angular/router';
import { AuthService } from '../auth.service';


@Injectable({
  providedIn: 'root'
})
export class AddvendorService {
  formData:Vendor;

  constructor(private _httpService: HttpClient,private router:Router,private authService:AuthService) { }
  getAllVendors():Observable<Vendor[]>{
    return this._httpService.get<Vendor[]>
    (environment.APIURL+'/vendor');      
  }

  insertVendor(vendor:Vendor){
    let body = JSON.stringify(vendor);
    console.log(vendor);
    let headers = new HttpHeaders({'Content-Type': 'application/json'});
    let options = { headers: headers }
    console.log('YES');
    if(vendor.vendorId){
      return this._httpService.put(environment.APIURL+'api/updatevendor/',body,options);
    }
    else{
    
      return this._httpService.post(environment.APIURL +'api/VendorPerson/insert', body, options);
    }

 
     
}
getVendorById(vendorId:any):Observable<Vendor>{
  return this._httpService.get<Vendor>
  (environment.APIURL+'api/vendorById/'+vendorId); 
}
logout(){
  this.authService.logout();
}
}
