import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Vendor } from './vendor';
import { environment } from 'src/environments/environment';
import { Router } from '@angular/router';


@Injectable({
  providedIn: 'root'
})
export class VendorService {
  formData:Vendor;
  vendor:Vendor;
  vendors:Vendor;

  constructor(private httpService: HttpClient,private router:Router) { }

  getAllVendors():Observable<Vendor[]>{return this.httpService.get<Vendor[]>(environment.APIURL+'/api/viewallvendors');      
  }
   //inorder to disable a particular vendor
   disableVendor(vendorId:any){
    let body = JSON.stringify(vendorId);
    let headers = new HttpHeaders({'Content-Type': 'application/json'});
    let options= { headers:headers}
    return this.httpService.put<Vendor[]>(environment.APIURL+'/api/disableVendor/'+vendorId,body,options);
  }
  logout(){
    this.router.navigate(['/login']);
  }

  getVendorbyId(vId:any): Observable<Vendor> {
    return this.httpService.get<Vendor>(environment.APIURL + '/contactDetails/' + vId)
  }

  getAllContacts(id:any):Observable<Vendor[]>{
    return this.httpService.get<Vendor[]>(environment.APIURL+'api/viewallcontacts/'+id);
  }

 
 
}