import { Component, OnInit } from '@angular/core';
import { Vendor } from './vendor';
import { VendorService } from './vendor.service';
import { Router } from '@angular/router';
import { AuthService } from '../auth.service';


@Component({
  selector: 'app-vendor',
  templateUrl: './vendor.component.html',
  styleUrls: ['./vendor.component.scss']
})
export class VendorComponent implements OnInit {

  vendors:Vendor[];
  vendor=new Vendor();
  searchId:any;
  view:any;

  constructor(private service: VendorService,private router :Router,private authService:AuthService) { }

  ngOnInit() {
    this.getVendor()
  }

  getVendor():void{
    this.service.getAllVendors()
    .subscribe((data)=>{
      this.vendors=data;
      console.log(data);

    },(error)=>{
      console.log(error);

    });
  }

  addVendor(){
    this.router.navigate(['addVendor']);
  }
  editVendor(id:any){
    this.router.navigate(['addVendor',id]);
  }
   
   PassId(searchvendor:any):void{
    this.searchId=searchvendor.vendorId;
  }
    //inorder to disable a particular vendor
    DisableVendor(dId:any):void{
      this.service.disableVendor(this.searchId)
      .subscribe((response)=>{
        console.log(response);
        this.getVendor();
      })
    }
    
    // onSelect1(selectVendor: any) {
    //   console.log("Selected vendor Id:", selectVendor.vendorId);
    //   this.view = selectVendor.vendorId;
    //   this.viewContact;
    //   this.viewContact(this.view);
    // }

    onSelect1(id:any){
      this.router.navigate(['viewcontact',id])
    }
    viewContact(data){
      this.router.navigate(['viewcontact',this.view])
    }
    backtoview(){
      this.router.navigate(['/vendor']); 
    }
    logout(){
      this.authService.logout();
    }

}
