import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { VendorComponent } from './vendor/vendor.component';
import { AddvendorComponent } from './addvendor/addvendor.component';
import { ContactComponent } from './vendor/view-contact/view-contact.component';
import { AuthGuard } from './auth.guard';



const routes: Routes = [
  {
    path:'',
    redirectTo: 'login', pathMatch: 'full'
  },
  
  {
    //login Page
    
    path:'login',
    component:LoginComponent
  },
{
  path:'vendor',
  component : VendorComponent,canActivate: [AuthGuard]
},
{
  path:'viewcontact/:id',
  component:ContactComponent,canActivate: [AuthGuard]
},

{
  path:'addVendor',
  component : AddvendorComponent,canActivate: [AuthGuard]
},
{
  path:'addVendor/:id',
  component : AddvendorComponent,canActivate: [AuthGuard]
},
];
@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
